import React, { useState, useRef, useEffect } from "react";
import Navbar from "../Components/Navbar";
import JoditEditor from 'jodit-react';
import { useParams, useNavigate } from "react-router-dom";
import axios from "axios";

function EditBlog() {
    const editor = useRef(null);
    const params = useParams();
    const navigate = useNavigate();
    const [title, setTitle] = useState("");
    const [subtitle, setSubtitle] = useState("");
    const [content, setContent] = useState("");



    useEffect(() => {
        getBlog();
    }, [params.id]);

    async function getBlog() {
        try {
            const response = await axios.get(`http://localhost:3001/blog_api/${params.id}`);
            if (response.data) {
                const { title, subtitle, content } = response.data.blog[0];
                setTitle(title);
                setSubtitle(subtitle);
                setContent(content);
            }
            console.log(response.data.blog[0])
        } catch (error) {
            console.log(error);
        }
    }

    async function handleSubmit(event) {
        try {
            event.preventDefault();
            const resopnse = await axios.post(`http://localhost:3001/edit-blog/${params.id}` ,{
                title, subtitle, content
            });
            if(resopnse){
                console.log("done");
                navigate("/user_blogs")
            }
        } catch (error) {
            console.log(error)
        }
       
    }

    return (
        <div>
            <Navbar />
            <div className="section-add_blog">
                <div className="add-blog">
                    <h1 className="add-blog__heading"> Edit blog</h1>
                    <hr className="add-blog__line" />
                    <form className="add-blog__form" encType="multipart/form-data" onSubmit={handleSubmit}>
                        <div className="add-blog__form__group">
                            <label className="add-blog__form__group__label" htmlFor="title">Title:</label>
                            <input className="add-blog__form__group__input" type="text"
                                value={title}
                                onChange={(e) => setTitle(e.target.value)}
                                placeholder="Enter title for the blog"
                                name="title"
                                required />
                        </div>
                        <div className="add-blog__form__group">
                            <label className="add-blog__form__group__label" htmlFor="subtitle">Subtitle:</label>
                            <input className="add-blog__form__group__input" type="text"
                                value={subtitle}
                                onChange={(e) => setSubtitle(e.target.value)}
                                placeholder="Enter subtitle for the blog"
                                name="subtitle"
                                required />
                        </div>
                        <div className="add-blog__form__group">
                            <label className="add-blog__form__group__label" htmlFor="content">Content:</label>
                            <JoditEditor
                                ref={editor}
                                value={content}
                                onChange={newContent => setContent(newContent)}
                                name="content" />
                        </div>
                        <div className="add-blog__form__btn-container">
                            <button type="submit" className="add-blog__form__button">Submit</button>
                        </div>
                    </form>
                </div>
            </div>
        </div>
    )
}

export default EditBlog;
