import React, { useState } from "react";
import Navbar from "../Components/Navbar";
import Footer from "../Components/Footer";
import { useNavigate } from "react-router-dom";
import axios from "axios";

function Login() {
    const [email, setemail] = useState("")
    const [password, setpassword] = useState("")
    const [msg, setmsg] = useState("")
    const navigate = useNavigate();

    const handlesubmit = async (event) => {
        event.preventDefault();
        try {
            const response = await axios.post("http://localhost:3001/login_api", { email, password });
            if (response) {
                localStorage.setItem("user", JSON.stringify(response.data.user))
                navigate("/")
            }
        } catch (error) {
            setmsg(error.response.data.msg)
            console.error(error);
        }
    }

    return <div>
        <Navbar />

        <section className="section-login">
            <div className="login">

                <h1 className="login_heading">Login.</h1>

                <form className="login_form" onSubmit={handlesubmit}>
                    <div className="login_form_input">
                        <label for="email">Email</label>
                        <input type="email"
                            value={email}
                            onChange={(event) => setemail(event.target.value)}
                            placeholder="Enter your email"
                            required />
                    </div>
                    <div className="login_form_input">
                        <label for="password">Password</label>
                        <input type="password"
                            value={password}
                            onChange={(event) => setpassword(event.target.value)}
                            placeholder="Enter your password"
                            required />
                    </div>

                    <button type="submit" className="login_form_button">Login</button>

                </form>
                {/* <hr className="login_line"></hr>
                <h1 className="login_form_button_google_heading">OR</h1>
                <button type="submit" className="login_form_button_google">Login with google</button> */}

            </div>

        </section>


   
    </div>
}

export default Login;