import React, { useRef, useState, useEffect, useMemo } from "react";
import Navbar from "../Components/Navbar";
import Footer from "../Components/Footer";
import JoditEditor from 'jodit-react';
import { useNavigate } from "react-router-dom";
import axios from 'axios';

function AddBlog() {
    const auth = localStorage.getItem("user")
    const editor = useRef(null);
    const [message, setmessage] = useState('');

    const [name, setname] = useState("")
    const [title, settitle] = useState("")
    const [subtitle, setsubtitle] = useState("")
    const [content, setcontent] = useState("")
    const [image, setimage] = useState("")
    const [date, setdate] = useState("")

    useEffect(() => {
        setname(JSON.parse(auth).name);
        setdate(formatDate());
    }, [auth]);


    const navigate = useNavigate();

    async function handleSubmit(event) {
        event.preventDefault();
        const response = await axios.post("http://localhost:3001/add_blog_api", {
            name, title, subtitle, content, image, date
        })
        if (response.status === 200) {
            navigate("/");
        }
    }



    function formatDate() {
        const d = new Date();
        const year = d.getFullYear();
        let month = '' + (d.getMonth() + 1);
        let day = '' + d.getDate();

        if (month.length < 2)
            month = '0' + month;
        if (day.length < 2)
            day = '0' + day;

        return [year, month, day].join('-');
    };

    const handleFileChange = async (event) => {
        setmessage("Uploading")
        const file = event.target.files[0];
        const imageFormData = new FormData();
        imageFormData.append('image', file);
        try {
            const imageUploadResult = await axios.post('http://localhost:3001/upload', imageFormData, {
                headers: {
                    'Content-Type': 'multipart/form-data'
                }
            });
            setmessage("File Uploaded")
            console.log('Image uploaded successfully:', imageUploadResult);
            console.log(imageUploadResult.data.url)
            setimage(imageUploadResult.data.url);
        } catch (error) {
            console.error('Error:', error);
        }
    };

    return (

        <div>
            <Navbar />
            <div className="section-add_blog">
                <div className="add-blog">

                    <h1 className="add-blog__heading">Add the blog</h1>
                    <hr className="add-blog__line" />

                    <form className="add-blog__form" onSubmit={handleSubmit} encType="multipart/form-data" >
                 
                        <div className="add-blog__form__group">
                            <label className="add-blog__form__group__label" for="email">Title:</label>
                            <input className="add-blog__form__group__input" type="text"

                                value={title}
                                onChange={(e) => settitle(e.target.value)}
                                placeholder="Enter title for the blog"
                                name="title"
                                required />
                        </div>

                        <div className="add-blog__form__group">
                            <label className="add-blog__form__group__label" for="email">Subtitle:</label>
                            <input className="add-blog__form__group__input" type="text"

                                value={subtitle}
                                onChange={(e) => setsubtitle(e.target.value)}
                                placeholder="Enter subtitle for the blog"
                                name="subtitle"
                                required />
                        </div>

                        <div className="add-blog__form__group">
                            <label className="add-blog__form__group__label" for="password">Content:</label>
                            <JoditEditor
                                ref={editor}
                                value={content}
                                onChange={newContent => setcontent(newContent)}
                                name="content" />
                        </div>

                        <div className="add-blog__form__group">
                            <label className="add-blog__form__group__label" for="email">Photo: (Upload photo for the blog)</label>
                            <input className="add-blog__form__group__input add-blog__form__group__input-file" type="file"
                                onChange={handleFileChange}
                                accept="image/*"
                                name="photo"
                            />
                            {message && <p>{message}</p>}
                        </div>

                        <div className="add-blog__form__btn-container">
                            <button type="submit" className="add-blog__form__button">Add blog</button>
                        </div>


                    </form>



                </div>

            </div>
     
        </div>

    )

}

export default AddBlog;


