import React from "react";
import { Link, useNavigate } from "react-router-dom";
import axios from "axios";

function Blog(props) {
    const { title, subtitle,editLink, id, del, edit, blogLink,author, date } = props;
    const navigate = useNavigate();



    const deleteBlog = async (id) => {
        try {
            const response = await axios.delete(`http://localhost:3001/delete_blog_api/${id}`)
            if (response) {
                navigate("/")
                window.location.reload();
            }
            console.log(id)
        } catch (error) {
            console.error(error);
        }
    }


    return (
        <div className="blog">
            <Link className="blog__link" to={blogLink}>
                <h1 className="blog__link--title">{title}</h1>
                <h2 className="blog__link--subtitle">{subtitle}</h2>
            </Link>
            <h2 className="blog__detail">Posted by {author} on {date}</h2>

            <div className="blog__edit"><Link className="blog__edit--btn" to={editLink}>{edit}</Link></div>
            <div className="blog__delete"><Link className="blog__delete--btn" onClick={() => deleteBlog(id)}>{del}</Link></div>
            <hr className="blog__line" />
        </div>
    );
}


export default Blog;



