import React, { useState, useEffect } from "react";
import Header from "../Components/Header";
import Footer from "../Components/Footer";
import Blog from "../Components/Blog";
import { useParams, Link } from "react-router-dom";
import axios from "axios";

function UserBlogPage() {
    const auth = localStorage.getItem("user")
    const [blogs, setBlogs] = useState([]);
    const [visibleBlogs, setVisibleBlogs] = useState(0);
    const params = useParams();

    useEffect(() => {
        getBlogs();
    }, [])



    async function getBlogs() {
        try {
            const response = await axios.get(`http://localhost:3001/user_blogs_api/${JSON.parse(auth).name}`)
            if (response) {
                setBlogs(response.data.blogs)
            }
        } catch (error) {
            console.error(error);
        }
    };

    const showNextBlogs = () => {
        setVisibleBlogs(prevVisibleBlogs => prevVisibleBlogs + 4);

    };

    const showPreviousBlogs = () => {
        setVisibleBlogs(prevVisibleBlogs => Math.max(0, prevVisibleBlogs - 4));

    };

    return (

        <div>
            <Header src={"/Images/Header/user-header.jpg"} text={JSON.parse(auth).name} alt={"header"} />
            <section className="user-blogs">
                <div className="user-blogs__addbutton">
                    <Link className="user-blogs__addbutton--btn" to="/add_blog">Add Blogs</Link>
                </div>


                {blogs.slice(visibleBlogs, visibleBlogs + 4).map((blog, index) => (

                    <Blog
                        key={index}
                        blogLink={blog._id}
                        title={blog.title}
                        subtitle={blog.subtitle}
                        edit={"Edit blog"}
                        editLink={`edit/${blog._id}`}
                        del={"Delete blog"}
                        id={blog._id}
                        author={blog.name}
                        date={blog.date.slice(0, 10)}
                    />
                ))}



                <div className="user-blogs--button">
                    <button className="index-blogs--button__btn1" onClick={showPreviousBlogs} style={{ visibility: visibleBlogs === 0 ? 'hidden' : 'visible' }} disabled={visibleBlogs === 0}>Previous page</button>
                    <button className="index-blogs--button__btn2" onClick={showNextBlogs} style={{ visibility: visibleBlogs + 4 >= blogs.length ? 'hidden' : 'visible' }} disabled={visibleBlogs + 4 >= blogs.length} >Next page</button>
                </div>

            </section>

            {/* <section className="section-user-blog">
               
                 
                {Blogs.slice(visibleBlogs, visibleBlogs + 4).map((blog, index) => (
           
                    <Blog
                        key={index}
                        blog_href={`blog/${blog._id}`}
                        title={blog.title}
                        subtitle={blog.subtitle}
                        author={blog.name}
                        date={blog.date}
                        link={"Edit blog"}
                        link_href= {`edit/${blog._id}`}
                        id={blog._id}
                        del_btn = {"Delete"}
                        
                    /> 
        
                ))}
            
                 <div className="section-user-blog__div-btn">
                <button className="section-user-blog__div-btn_btn" onClick={showPreviousBlogs} style={{ visibility: visibleBlogs === 0 ? 'hidden' : 'visible'}} disabled={visibleBlogs === 0}>Previous page</button>
                <button className="section-user-blog__div-btn_btn" onClick={showNextBlogs} style={{ visibility: visibleBlogs + 4 >= Blogs.length ? 'hidden' : 'visible' }} disabled={visibleBlogs + 4 >= Blogs.length} >Next page</button>
            </div>
            </section> */}



        </div>

    );
}

export default UserBlogPage;
