import React, { useState, useEffect } from "react";
import Blog from "../Components/Blog";
import Header from "../Components/Header";

import axios from "axios";

function Index() {
    const auth = localStorage.getItem("user")
    const [blogs, setblogs] = useState([]);
    const [visibleBlogs, setVisibleBlogs] = useState(0);

    useEffect(() => {
        getBlogs();
    }, []);

    async function getBlogs() {
        try {
            const response = await axios.get("http://localhost:3001/all_blogs_api")
            if (response.status === 200) {
                setblogs(response.data.allblogs);
            }
        } catch (error) {
            console.log(error)
        }
    };

    const showNextBlogs = () => {
        setVisibleBlogs(prevVisibleBlogs => prevVisibleBlogs + 4);

    };

    const showPreviousBlogs = () => {
        setVisibleBlogs(prevVisibleBlogs => Math.max(0, prevVisibleBlogs - 4));

    };




    return (
        <>
            <Header src={"/Images/Header/Index-header.jpg"} text={"BlogSphere"} alt={"header"} msg={auth ? "" : "Login to add your own blogs"} />
            <section className="index-blogs">

                {blogs.slice(visibleBlogs, visibleBlogs + 4).map((blog, index) => (

                    <Blog
                        key={index}
                        blogLink={`blog/${blog._id}`}
                        title={blog.title}
                        subtitle={blog.subtitle}
                        author={blog.name}
                        date={blog.date.slice(0, 10)}
                    />
                ))}


                <div className="index-blogs--button">
                    <button className="index-blogs--button__btn1" onClick={showPreviousBlogs} style={{ visibility: visibleBlogs === 0 ? 'hidden' : 'visible' }} disabled={visibleBlogs === 0}>Previous page</button>
                    <button className="index-blogs--button__btn2" onClick={showNextBlogs} style={{ visibility: visibleBlogs + 4 >= blogs.length ? 'hidden' : 'visible' }} disabled={visibleBlogs + 4 >= blogs.length} >Next page</button>
                </div>


            </section>
        </>
    )





}

export default Index;













