import React from "react";
import Navbar from "./Navbar";
import Blog from "./Blog";


function Header(props) {
    const { src, text,alt, msg, style} = props;
    return (
        <header className="header">
            
          
            
            <div className="header__img_container">
            <img className="header_img" src={src} alt={alt} />
            <div className="header__img_container__heading">
            <h1 className="header__img_container__heading-text">{text}</h1>
            <h1 className="header__img_container__heading-msg">{msg}</h1>
            </div>
            
            </div>
        </header>
    ) 
}

export default Header;


