import React, { useState, useEffect } from "react";
import Header from "../Components/Header";

import { useParams } from "react-router-dom";
import axios from "axios";


function BlogPage() {
    const [blog, setBlog] = useState([]);
    const [tagLength, setTagLength] = useState(0);
    const [margin, setmargin] = useState("");
    const params = useParams();

    useEffect(() => {
        getBlogs();
        const tag = document.getElementById("content"); 
        if (tag) {
            const tagLength = tag.innerHTML.length;
            setTagLength(tagLength);
            {tagLength < 1000 ? setmargin("section-blogpage__short"):setmargin("section-blogpage__long") }
        }
    }, [blog, params.id]); 


    async function getBlogs() {
        try {
            const response = await axios.get(`http://localhost:3001/blog_api/${params.id}`);
            if (response) {
                setBlog(response.data.blog[0])
            }
        } catch (error) {
            console.error(error);
        }
    }



    return (
        <div>
            {blog.image === "" ?
             <Header src={"/Images/Header/Index-header.jpg"} text={""} alt={"header"} /> :
             <Header src={blog.image} text={""} alt={"header"} />}
           
 
            <section className="section-blogpage">
                <div className="section-blogpage__title">
                    <h1 >{blog.title}</h1>
                </div>

                <h1  className="section-blogpage__subtitle">{blog.subtitle}</h1>
               
                
                <div id="content" className={`section-blogpage__content ${margin}`}  dangerouslySetInnerHTML={{ __html: blog.content }}></div>
           
            </section>


        </div>

    );
}

export default BlogPage;
